import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { Col, Container, Row, Button, Modal, Form } from "react-bootstrap"
import axios from 'axios'
import base_url from "../../api/api";
import { useSelector } from "react-redux";
import Sidebar from "../Sidebar";
import DataTable from "react-data-table-component";
import swal from "sweetalert";
import VisibilityIcon from '@mui/icons-material/Visibility';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@mui/icons-material/Edit';

function ViewGroup() {
    const [groupMemberList, setGroupMemberList] = useState([])
    const [mediaList, setMediaList] = useState([])

    const [show, setShow] = useState(false);
    const [pending, setPending] = useState(true);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    const [user, setUser] = useState({
        name: '',
        image: '',
        expiring_date: '',
        grp_join_appr_wall: '',
        is_start_now: '',
        strt_date: '',
        pin_count: '',
        likes_count: '',
        is_freeze: '',
        qr_code: '',
        key_qrcode: '',
        is_disable_qr_code: '',
        status: '',
        created_by: '',
        creation_time: '',
        update_time: '',
        _id: '',
        id: '',
    });
    const [groupMember, setGroupMember] = useState({
        group_id: '',
        group_users: ''
    })

    function GroupMember(e) {
        const newgroupMember = { ...groupMember }
        newgroupMember[e.target.id] = e.target.value
        setGroupMember(newgroupMember)
        // console.log(newgroupMember)
    }
    // debugger
    const params = useParams();
    useEffect(() => {
        const timeout = setTimeout(() => {
            getGroupData();
            stratNow();
            getMediaList();
            getGroupMemberList();
            setPending(false);
        }, 2000);
        return () => clearTimeout(timeout);
    }, []);
    const jwt_token = sessionStorage.getItem('token');
    const getGroupData = async () => {
        // debugger
        axios.get(base_url + `groups/get_group_detail?group_id=${params.Id}`,
            {
                headers: {
                    jwt: jwt_token
                },
            }
        ).then(res => {
            // console.log(res.data.data)
            setUser(res.data.data)
        })

    };
    const getMediaList = async () => {
        axios.post(base_url + `groups/group_media_list`,
            {
                group_id: `${params.Id}`
            },
            {
                headers: {
                    jwt: jwt_token
                },
            }).then(res => {
                // console.log(res.data.data)
                setMediaList(res.data.data)
            })
    }
    const getGroupMemberList = async () => {
        axios.post(base_url + `groups/group_member_list`,
            {
                group_id: params.Id
            },
            {
                headers: {
                    jwt: jwt_token
                },
            }).then(res => {
                console.log(res.data.data)
                setGroupMemberList(res.data.data)
            })
    }

    const active = useSelector((state) => state.toggleSidebar.active);
    let grp_strt_now = '';
    function stratNow() {
        if (user.is_start_now === '0') {
            return (
                grp_strt_now = "Schedule"
            )
        } else {
            return (
                grp_strt_now = "Start Now"
            )
        }
    }
    let grp_access = '';
    function GroupAccess() {
        if (user.grp_join_appr_wall === '1') {
            return (
                grp_access = "Immediate Access"
            )
        } else {
            return (
                grp_access = "Request Approval"
            )
        }
    }
    const AddAdmin = async (id, user_id, status) => {
        debugger
        axios.post(base_url + 'groups/add_group_admin', {
            group_id: params.Id,
            user_id,
            is_admin: status,
            req_id: id
        }, {
            headers: {
                jwt: jwt_token
            }
        }).then(res => {
            console.log(res)
            if (res.data.status === true) {
                // navigate("/user_details");
                window.location.reload()
            }
        })
        // alert(id)
    }
    const RemoveAdmin = async (id, user_id, status) => {
        debugger
        axios.post(base_url + 'groups/remove_group_admin', {
            group_id: params.Id,
            user_id,
            is_admin: status,
            req_id: id
        }, {
            headers: {
                jwt: jwt_token
            }
        }).then(res => {
            console.log(res)
            if (res.data.status === true) {
                // navigate("/user_details");
                window.location.reload()
            }
        })
        // alert(id)
    }
    const UpdateGroupStatus = async (id, status) => {
        axios.post(base_url + 'users/update_user', {
            user_id: id,
            status
        }).then(res => {
            if (res.data.status === true) {
                // navigate("/user_details");
                window.location.reload()
            }
        })
        // alert(id)
    }
    const group_member_columns = [
        {
            name: "Id",
            sortable: true,
            selector: (row) => row.user_id,
        },
        {

            name: "Name",
            sortable: true,
            selector: (row) => row.name,
        },
        {
            name: "Email",
            sortable: true,
            selector: (row) => row.email,
        },
        {
            name: "Mobile",
            sortable: true,
            selector: (row) => row.mobile,
        },
        {
            name: "Admin",
            sortable: true,
            selector: (row) => {
                var AdminStatus = '1';
                if (row.is_admin === '1') {
                    AdminStatus = "0";
                    return (
                        <Button className="btn btn-success" onClick={() => RemoveAdmin(row.id, row.user_id, AdminStatus)}>Admin</Button>

                    )
                } else if (row.is_admin === '0') {
                    return (
                        <Button className="btn btn-primary" onClick={() => AddAdmin(row.id, row.user_id, AdminStatus)}>Make Admin</Button>
                    )
                }

            },
        },
        {
            name: "Action",
            selector: (row) => {
                return (
                    <div>
                        {/* <Link to={"/edit_group/" + row.id}>
                            <EditIcon />
                        </Link>
                        <Link to={'/view_group/' + row.id}>
                            <VisibilityIcon />
                        </Link > */}
                        <DeleteIcon className='usr_dltlist'
                        // onClick={() => hendleDelete(row.id)} 
                        />
                    </div>
                )
            },
        }
    ]
    const group_media_columns = [
        {
            name: "Id",
            selector: (row) => row.id,
            sortable: true
        },
        {

            name: "Image",
            selector: (row) =>
                <div style={{ width: '100%', height: '70px', overflow: 'hidden' }}>
                    <img src={row.media_url} className="img-fluid" alt="Photo_Bomb" />
                </div>,
            sortable: true
        },
        {
            name: "Total Likes",
            selector: (row) => row.likes_count,
            sortable: true
        },
        {
            name: "Total Comment",
            selector: (row) => row.comment_count,
            sortable: true
        },
        {
            name: "Action",
            sortable: true,

        },
        {
            name: "Action",
            selector: (row) => {
                return (
                    <div>
                        {/* <Link to={"/edit_group/" + row.id}>
                            <EditIcon />
                        </Link>
                        <Link to={'/view_group/' + row.id}>
                            <VisibilityIcon />
                        </Link > */}
                        <DeleteIcon className='usr_dltlist'
                            onClick={() => DeleteMedia(row.id)}
                        />
                    </div>
                )
            },
        }

    ]

    const DeleteMedia = async (id) => {
        swal({
            title: "Are you sure?",
            // text: "Once deleted, you will not be able to recover this imaginary file!",
            icon: "warning",
            buttons: true,
            dangerMode: true,
        })
            .then((willDelete) => {
                if (willDelete) {
                    swal("User Deleted Successfully!",
                        setMediaList(mediaList.filter((item) => item.id !== id)),
                        axios.post(base_url + 'groups/delete_group_media', {
                            user_id: id
                        }), {
                        icon: "success",
                    });
                }
            });
    }
    function AddGroupMember(e) {
        e.preventDefault();
        axios.post(base_url + 'groups/add_group_member', {
            group_id: groupMember.group_id,
            group_users: groupMember.group_users
        },
            {
                headers: {
                    jwt: jwt_token
                },
            })
            .then(res => {
                console.log(res.data)
                if (res.data.status === true) {
                    swal('Group Member Added Successfully!', {
                        timer: 2000,
                        buttons: false,
                        icon: 'success'
                    }).then(
                        // navigate("/group_list")
                        window.location.reload()
                    )
                }
                else {
                    swal(res.data.message, {
                        icon: 'warning',
                    })
                    // setErrMsg(res.data.message)
                }
                // <p>{errMsg}</p>
            })
    }


    return (
        <>
            <Sidebar />
            <div className={active ? 'content-mobile' : 'content'}>
                <Container fluid className="mt-3 mb-5">
                    <Row>
                        <Col><h4>User Details</h4></Col>
                    </Row>
                    <Row>
                        <Col lg="3">
                            <img src={user.image} className="img-fluid" />
                        </Col>
                        <Col lg="9">
                            <Row>
                                <Col lg="6">
                                    <p><strong>Id : </strong >{user.id}</p>
                                </Col>
                                <Col lg="6">
                                    <p><strong>Name : </strong>{user.name}</p>
                                </Col>
                            </Row>
                            <Row>
                                <Col lg="6">
                                    <p><strong>Expiry Date : </strong>{user.expiring_date}</p>
                                </Col>
                                <Col lg="6">
                                    <p><strong>User Access : </strong>{GroupAccess(grp_access)}</p>
                                </Col>
                            </Row>
                            <Row>
                                <Col lg="6">
                                    <p><strong>Group Start : </strong>{stratNow(grp_strt_now)}</p>
                                </Col>
                                <Col lg="6">
                                    <p><strong>Start Date : </strong>{user.strt_date}</p>
                                </Col>
                            </Row>
                            <Row>
                                <Col lg="6">
                                    <p><strong>Created On : </strong>{new Intl.DateTimeFormat('en-US').format(user.creation_time)}</p>
                                </Col>
                                <Col lg="6">
                                    <p><strong>Last Login : </strong>{user.last_login}</p>
                                </Col>
                            </Row>
                        </Col>
                    </Row>
                    <Row>
                        <Col lg="6" className="mt-3"><h4>Group Member List</h4></Col>
                        <Col lg="6" className="mt-3" style={{ textAlign: 'right' }}>
                            <Button variant="primary" onClick={handleShow} className="mb-2 btn-success">
                                Add Group Member
                            </Button>

                            <Modal size="xl" show={show} onHide={handleClose}>
                                <Modal.Header closeButton>
                                    <Modal.Title>Add Group Member</Modal.Title>
                                </Modal.Header>
                                <Modal.Body>
                                    <Form onSubmit={(e) => AddGroupMember(e)}>
                                        <Row>
                                            <Col lg="6">
                                                <Form.Group className="mb-3">
                                                    <Form.Label>Group Id</Form.Label>
                                                    <Form.Control
                                                        type="text"
                                                        placeholder="Group Id"
                                                        id="group_id"
                                                        onChange={(e) => GroupMember(e)}
                                                    />
                                                </Form.Group>
                                            </Col>
                                            <Col lg="6">
                                                <Form.Group className="mb-3">
                                                    <Form.Label>Group Users</Form.Label>
                                                    <Form.Control
                                                        type="text"
                                                        placeholder="User Id"
                                                        id="group_users"
                                                        onChange={(e) => GroupMember(e)}
                                                    />
                                                </Form.Group>
                                            </Col>
                                        </Row>
                                        <Button variant="primary" type="submit" className="btn-success">
                                            Add User
                                        </Button>
                                    </Form>
                                </Modal.Body>
                            </Modal>
                        </Col>
                    </Row>
                    <Row>
                        <Col>
                            <DataTable progressPending={pending} columns={group_member_columns} data={groupMemberList} pagination={Number} fixedHeader fixedHeaderScrollHeight="450" />
                        </Col>
                    </Row>
                    <Row className="mt-5">
                        <Col><h4>Comments and likes on each Image</h4></Col>
                    </Row>
                    <Row>
                        <Col>
                            <DataTable progressPending={pending} columns={group_media_columns} data={mediaList} pagination={Number} fixedHeader fixedHeaderScrollHeight="450" />
                        </Col>
                    </Row>
                </Container>
            </div>
        </>
    );
}


export default ViewGroup;