import React, { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import { Col, Container, Row, Table } from "react-bootstrap"
import axios from 'axios'
import base_url from "../../api/api";
import Sidebar from "../Sidebar";
import { useSelector } from "react-redux";

function Updateplan() {
  const navigate = useNavigate();
    const [user, setUser] = useState({
      id: '',
      validity: '',
      paid_price: '',
      paid_data: '',
    });
    function UpdateUser(e) {
        const updateuser = { ...user }
        updateuser[e.target.id] = e.target.value
        console.log(updateuser)
        setUser(updateuser)
    }

    const jwt_token = sessionStorage.getItem('token')
    function SubmitUpdatedUser(e) {
        e.preventDefault();
        axios.post(base_url + 'plans/update_plan', {
          plan_id: `${user.id}`,
          plan_name: user.plan_name,
          validity: user.validity,
          paid_price: user.paid_price,
          paid_data: user.paid_data,
        },{
          headers:{
            jwt:jwt_token
          }
        })
            .then(res => {
                if (res.data.status === true) {
                    navigate("/plans");
                    // window.location.reload()
                }
                else {
                    alert(res.data.message)
                }
                // <p>{errMsg}</p>
            })
    }
    const params = useParams();
    useEffect(() => {
        getUserWithFetch();
    }, []);
    const getUserWithFetch =async ()=>{
      axios.get(base_url + 'plans/plans_details?plan_id='+ params.Id,{
        headers:{
          jwt:jwt_token
        }
      }).then(res=>{
        setUser(res.data.data)
        // console.log(res.data.data)
      })
    }

    const active = useSelector((state) => state.toggleSidebar.active);

    return (
        <>
            <Sidebar />
            <div className={active ? 'content-mobile' : 'content'}>
                <Container fluid className="mb-5 mt-3">
                    <Row>
                        <Col><h4>Update Plan</h4></Col>
                    </Row>
                    <Row>
                        <Col lg="3">
                            <img src={user.profile_picture} className="img-fluid" />
                        </Col>
                        <Col lg="9">
                            <form onSubmit={(e) => SubmitUpdatedUser(e)}>
                                <Row>
                                    <Col lg={4}>
                                        <label>Id :</label>
                                        <input type='text'  disabled id="id" value={user.id} className='form-control mb-3'></input>
                                    </Col>
                                    <Col lg={4}>
                                        <label>Validity :</label>
                                        <input placeholder='Validity' id="validity" onChange={(e) => UpdateUser(e)} value={user.validity} className='form-control mb-3' type='text'></input>
                                    </Col>
                                    <Col lg={4}>
                                        <label>Paid Price :</label>
                                        <input placeholder='$' id="paid_price" onChange={(e) => UpdateUser(e)} value={user.paid_price} className='form-control mb-3' type='text'></input>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col lg={4}>
                                        <label>Plan Data :</label>
                                        <input placeholder='Plan Data' id="paid_data" onChange={(e) => UpdateUser(e)} value={user.paid_data} className='form-control mb-3' type='text'></input>
                                    </Col>
                                </Row>
                                <Row>
                                    <Col lg={12}>
                                        <button type='submit'>Update</button>
                                    </Col>
                                </Row>
                            </form>
                        </Col>
                    </Row>
                </Container>
            </div>
        </>
    )
}

export default Updateplan