import React, { Suspense, lazy, useState } from 'react';
// import './component/Header.css'
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import PublicRoute from './Utils/PublicRoutes';
import PrivateRoute from './Utils/PrivateRoutes';
import PageNotFound from './component/PageNotFound';
import UserTable from './component/user/UserTable';
import ViewUser from './component/user/ViewUser';
import AddNewUser from './component/user/AddNewUser';
import AOS from 'aos';
import "aos/dist/aos.css";
import UpdateUser from './component/user/UpdateUser';
import GroupList from './component/groups/GroupList';
import ViewGroup from './component/groups/ViewGroup';
import UpdateGroup from './component/groups/UpdateGroup';
import AddGroup from './component/groups/AddGroup';
import SendOtp from './component/SendOtp';
import VerifyOtp from './component/VerifyOtp';
import Plans from './component/plans/Plans';
import Updateplan from './component/plans/Updateplan';
const Login = lazy(() => import('./component/Login'));
const Dashboard = lazy(() => import('./component/Dashboard'));
// import cors from 'cors';

function App() {

  AOS.init();

  return (
    <div className="App">
        <Router>
          {/* <SideBar> */}
            <Suspense fallback={<div>Loading...</div>}>
              <Routes>
                <Route path="/" element={<PublicRoute />}>
                  <Route path="/" element={<Login />} />
                  <Route path="/forget_password" element={<SendOtp />} />
                  <Route path="/verify_otp" element={<VerifyOtp />} />
                </Route>

                <Route path="/" element={<PrivateRoute />}>
                  <Route path="/dashboard" element={<Dashboard />} />
                  <Route path="/user_details" element={<UserTable />} />
                  <Route path="/user/:Id" element={<ViewUser />} />
                  <Route path="/add_user" element={<AddNewUser />} />
                  <Route path="/edit_user/:Id" element={<UpdateUser />} />
                  <Route path="/group_list" element={<GroupList />} />
                  <Route path="/view_group/:Id" element={<ViewGroup />} />
                  <Route path="/edit_group/:Id" element={<UpdateGroup />} />
                  <Route path="/add_group" element={<AddGroup />} />
                  <Route path="/plans" element={<Plans />} />
                  <Route path="/edit_plan/:Id" element={<Updateplan />} />
                </Route>
                <Route path="/:pageName" element={<PageNotFound />} />
              </Routes>
            </Suspense>
          {/* </SideBar> */}

        </Router>
    </div>
  );
}

export default App;
