import React, { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import { Container, Button, Accordion } from "react-bootstrap";
import { Link } from 'react-router-dom'
import VisibilityIcon from '@mui/icons-material/Visibility';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@mui/icons-material/Edit';
import Sidebar from "../Sidebar";
import { useSelector } from "react-redux";
import base_url from "../../api/api";
import axios from "axios";
import Addplan from "./Addplan"
import swal from "sweetalert";

const Plans = () => {

  const [userData, setUserData] = useState([]);
  useEffect(() => {
    getUserWithFetch();
  }, []);
  const jwt_token = sessionStorage.getItem('token')
  const getUserWithFetch = async () => {
    axios.get(base_url + 'plans/get_user_plan', {
      headers: {
        jwt: jwt_token
      }
    }).then(res => {
      console.log(res)
      if (res.data.status === true) {
        // navigate("/user_details");
        //   window.location.reload()
        setUserData(res.data.data)
      }
      else {
        alert(res.data.plan_name)
        setUserData(res.data.plan_name)
      }
      // <p>{errMsg}</p>
    });

  };

  const hendleDelete = async (id) => {
    debugger
    swal({
      title: 'Are you sure?',
      icon: 'warning',
      buttons: true,
      dangerMode: true,
    })
      .then((willDelete) => {
        if (willDelete) {
          swal('Plan Deleted Successfully!',
            setUserData(userData.filter((item) => item.id !== id)),
            axios.post(base_url + 'plans/plans_delete', {
              plan_id: `${id}`
            }, {
              headers: {
                jwt: jwt_token
              }
            })
          )
        }
      })
  }
  const active = useSelector((state) => state.toggleSidebar.active);

  const columns = [
    {
      name: 'Id',
      selector: (row) => row.id,
      sortable: true
    },
    // {
    //   name: 'Plane Name',
    //   selector: (row) => row.plan_name
    // },
    {
      name: 'Validity',
      selector: (row) => row.validity,
      sortable: true
    },
    {
      name: 'Plan Price',
      selector: (row) => row.paid_price,
      sortable: true
    },
    {
      name: 'Data',
      selector: (row) => row.paid_data,
      sortable: true
    },
    {
      name: 'Created On',
      selector: (row) => Intl.DateTimeFormat('en-US', { year: 'numeric', month: '2-digit', day: '2-digit', hour: '2-digit', minute: '2-digit', second: '2-digit' }).format(row.creation_time),
      sortable: true
    },
    {
      name: 'Updated On',
      selector: (row) => Intl.DateTimeFormat('en-US', { year: 'numeric', month: '2-digit', day: '2-digit', hour: '2-digit', minute: '2-digit', second: '2-digit' }).format(row.modified_time),
      sortable: true
    },
    {
      name: "Action",
      selector: (row) => {
        return (
          <div>
            <Link to={"/edit_plan/" + row.id}>
              <EditIcon />
            </Link>
            <DeleteIcon className='usr_dltlist' onClick={() => hendleDelete(row.id)} />
          </div>
        )
      },
    },
  ]
  return (
    <>
      <Sidebar />
      <div className={active ? 'content-mobile' : 'content'}>
        <Container fluid className="mb-5">
          <Accordion defaultActiveKey="0" className="mb-4 mt-2">
            <Accordion.Item eventKey="1">
              <Accordion.Header>ADD User</Accordion.Header>
              <Accordion.Body>
                <Addplan />
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>
          <h1>Plan List</h1>
          {/* <div>{userData.data}</div> */}
          <DataTable columns={columns} data={userData} pagination={Number} fixedHeader fixedHeaderScrollHeight="450" />
        </Container>
      </div>
    </>
  )
}

export default Plans;

