import React, { useState } from 'react'
import axios from 'axios'
import { Col, Container, Form, Row } from 'react-bootstrap'
import base_url from '../../api/api';
import S3FileUpload from 'react-s3';
import { useNavigate } from 'react-router-dom';
import { useSelector } from "react-redux";
import Sidebar from "../Sidebar";
import swal from 'sweetalert';
import Compressor from 'compressorjs';
window.Buffer = window.Buffer || require("buffer").Buffer;

const config = {
    bucketName: 'photobomb-1',
    dirName: 'group_profile', /* optional */
    region: 'us-east-1',
    accessKeyId: 'AKIA2XVW5RN2BOZPCDPG',
    secretAccessKey: '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',
}

export default function AddGroup() {
    const navigate = useNavigate();
    const [grpProfile, setGrpProfile] = useState()
    
    const [user, setUser] = useState({
        name: '',
        expiring_date: '',
        grp_join_appr_wall: '',
        strt_date: '',
        is_start_now: '',
        image: '',
        group_users_mobile: '',
        user_id: ''
    })
    const jwt_token = sessionStorage.getItem('token');
    const [errMsg, setErrMsg] = useState()
    // console.log(usrProfile)
    function submit(e) {
        e.preventDefault();
        axios.post(base_url + 'groups/create_group', {
            name: user.name,
            expiring_date: user.expiring_date,
            grp_join_appr_wall: user.grp_join_appr_wall,
            strt_date: user.strt_date,
            is_start_now: user.is_start_now,
            image: grpProfile,
        },
            {
                headers: {
                    jwt: jwt_token
                },
            })
            .then(res => {
                // console.log(res.data)
                if (res.data.status === true) {
                    swal('Group Created Successfully!', {
                        timer: 1500,
                        buttons: false,
                        icon: 'success'
                    }).then(
                        navigate("/group_list")
                    )
                    // window.location.reload()
                }
                else {
                    swal(res.data.message,{
                        icon:'warning',
                        buttons:false,
                        timer:1000
                    })
                    // setErrMsg(res.data.message)
                }
                // <p>{errMsg}</p>
            })
    }

    function Group(e) {
        const newuser = { ...user }
        newuser[e.target.id] = e.target.value
        setUser(newuser)
        // console.log(newuser)
        // StartNow(strt_now);
    }
    function upload(e) {
        S3FileUpload.uploadFile(e.target.files[0], config)
            .then((data) => {
                setGrpProfile(data.location)                
            })
            .catch((err) => {
                console.log(err)
            })
    }
    let strt_now = true;
    function StartNow() {
        if (user.is_start_now === '1') {
            return (
                strt_now = true
            )
        } else if (user.is_start_now === '0') {
            return (
                strt_now = false
            )
        }
    }
    StartNow(strt_now);
    const active = useSelector((state) => state.toggleSidebar.active);

    return (
        <>
            <Sidebar />
            <div className={active ? 'content-mobile' : 'content'}>
                <Container fluid className="mb-5">
                    <h4>Add Group</h4>
                    <form onSubmit={(e) => submit(e)}>
                        <Row>
                            <Col lg={4}>
                                <label>Group Name :</label>
                                <input placeholder='Name' onChange={(e) => Group(e)} id="name" value={user.name} className='form-control mb-3' type='text'></input>
                            </Col>
                            <Col lg={4}>
                                <label>Group Expiry :</label>
                                <Form.Select onChange={(e) => Group(e)} id="expiring_date" value={user.expiring_date}>
                                    <option value="null">Please Select Expiry Date</option>
                                    <option value="48 Hours">48 Hours</option>
                                    <option value="No Expiration">No Expiration</option>
                                </Form.Select>
                            </Col>
                            <Col lg={4}>
                                <label>User Access :</label>
                                <Form.Select onChange={(e) => Group(e)} id="grp_join_appr_wall" value={user.grp_join_appr_wall}>
                                    <option value="null">Please Select Access</option>
                                    <option value="1">Immediate</option>
                                    <option value="0">Request Approval</option>
                                </Form.Select>
                            </Col>
                        </Row>
                        <Row>
                            <Col lg={4}>
                                <label>Select Image :</label>
                                <input type='file' onChange={upload} id="image" className='form-control mb-3'></input>
                            </Col>
                            <Col lg={4}>
                                <label>Group Start :</label>
                                <Form.Select onChange={(e) => Group(e)} id="is_start_now" value={user.is_start_now}>
                                    <option value="null">Please Select Start Group</option>
                                    <option value="1">Start Now</option>
                                    <option value="0">Schedule</option>
                                </Form.Select>
                            </Col>
                            {
                                strt_now ? "" :
                                    <Col lg={4}>
                                        <label>Start Date/Time :</label>
                                        <input className='form-control' id="strt_date" onChange={(e) => Group(e)} value={user.strt_date} />
                                        {/* <DateTimePicker className="form-control" id="strt_date"/> */}
                                    </Col>
                            }

                        </Row>
                        {/* <Row>
                            <Col lg={4}>
                                <label>Add Group Admin :</label>
                                <input className='form-control' id="admin_id" onChange={(e) => GroupAdmin(e)} value={adminUser.admin_id} />
                            
                            </Col>
                        </Row> */}
                        <Row>
                            <Col lg={12}>
                                <button type='submit'>Submit</button>
                            </Col>
                        </Row>
                    </form>
                </Container>
            </div>
        </>
    )
}
