import React, { useEffect, useState } from "react";
import DataTable from "react-data-table-component";
import { Container, Button, Accordion } from "react-bootstrap";
import { Link } from 'react-router-dom'
import VisibilityIcon from '@mui/icons-material/Visibility';
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@mui/icons-material/Edit';
import Sidebar from "../Sidebar";
import { useSelector } from "react-redux";
import axios from "axios";
import swal from 'sweetalert';
import base_url from "../../api/api";

const GroupList = () => {
  // debugger;

  const [userData, setUserData] = useState([]);
  useEffect(() => {
    getUserWithFetch();
  }, []);

  const getUserWithFetch = async () => {
    const response = await fetch(base_url + 'groups/get_groups');
    const jsonData = await response.json();
    setUserData(jsonData.data);
    // console.log(response)

  };
  const jwt_token = sessionStorage.getItem('token');
  const hendleDelete = async (id) => {
    swal({
      title: "Are you sure?",
      // text: "Once deleted, you will not be able to recover this imaginary file!",
      icon: "warning",
      buttons: true,
      dangerMode: true,
    })
      .then((willDelete) => {
        if (willDelete) {
          swal("Group Deleted Successfully!",
            setUserData(userData.filter((item) => item.id !== id)),
            axios.post(base_url + 'groups/group_delete', {
              group_id: id
            },
              {
                headers: {
                  jwt: jwt_token
                },
              }), {
            icon: "success",
          });
        }
      });
  }
  const handleSort = (column, sortDirection) => {
    console.log(column.selector, sortDirection);
  }

  const UpdateGroupStatus = async (id, status) => {
    axios.post(base_url + 'groups/enable_disable_group', {
      group_id: id,
      status
    }).then(res => {
      if (res.data.status === true) {
        // navigate("/user_details");
        window.location.reload()
      }
    })
    // alert(id)
  }

  const active = useSelector((state) => state.toggleSidebar.active);

  const columns = [
    {
      name: "Id",
      selector: (row) => row.id,
    },
    {

      name: "Name",
      selector: (row) => row.name,
    },
    {
      name: "Start Date",
      selector: (row) => row.strt_date,
    },
    {
      name: "Expiring Date",
      selector: (row) => row.expiring_date,
    },
    {
      name: "Status",
      selector: (row) => {
        let userStatus = "1";
        if (row.status === '1') {
          userStatus = "0";
          return (
            <div className='usr_stats'>
              <Button className="disabl" onClick={() => UpdateGroupStatus(row.id, userStatus)}>Disabled</Button>
            </div>
          )
        } else if (row.status === '0') {
          return (
            <div className='usr_stats'>
              <Button className="activ" onClick={() => UpdateGroupStatus(row.id, userStatus)}>Active</Button>
            </div>
          )
        }

      },

    },
    {
      name: "Group Type",
      selector: (row) => {
        if (row.grp_join_appr_wall === '1') {
          return (
            <span>Immediate access</span>
          )
        } else if (row.grp_join_appr_wall === '0') {
          return (
            <span>Request Approval</span>
          )
        }
      },
    },
    {
      name: "Creaction Date",
      selector: (row) => {
        const unixTime = row.creation_time;
        const reald = new Intl.DateTimeFormat('en-US', { year: 'numeric', month: '2-digit', day: '2-digit', hour: '2-digit', minute: '2-digit', second: '2-digit' }).format(unixTime);
        return (
          <span>{reald}</span>
        )
      },
    },
    {
      name: "Action",
      selector: (row) => {
        return (
          <div>
            <Link to={"/edit_group/" + row.id}>
              <EditIcon />
            </Link>
            <Link to={'/view_group/' + row.id}>
              <VisibilityIcon />
            </Link >
            <DeleteIcon className='usr_dltlist' onClick={() => hendleDelete(row.id)} />
          </div>
        )
      },
    },
  ]

  return (
    <>
      <Sidebar />
      <div className={active ? 'content-mobile' : 'content'}>
        <Container fluid className="mb-5">
          <h4>Group List</h4>
          <DataTable columns={columns} data={userData} pagination={Number} fixedHeader fixedHeaderScrollHeight="450" />
        </Container>
      </div>
    </>
  )
}
export default GroupList;