import React, { useState } from 'react'
import axios from 'axios'
import { Col, Container, Row } from 'react-bootstrap'
import { useNavigate } from 'react-router-dom';
import base_url from '../../api/api';



export default function Addplan() {
    const navigate = useNavigate();
    const [usrProfile, setUsrProfile] = useState()
    const [user, setUser] = useState({
        plan_title: '',
        validity: '',
        paid_price: '',
        plan_data: '',
    })

    const [errMsg, setErrMsg] = useState();
    const jwt_token = sessionStorage.getItem('token')
    // console.log(usrProfile)
    function submit(e) {
        e.preventDefault();
        axios.post(base_url + 'plans/create_plan', {
            plan_name: user.plan_title,
            validity: user.validity,
            paid_price: user.paid_price,
            paid_data: user.plan_data,
        }, {
          headers: {
            jwt: jwt_token
        }})
        .then(res => {
          // console.log(res)
            if(res.data.status === true){
                // navigate("/user_details");
                window.location.reload()
            }
            else {
                alert(res.data.message)
                setErrMsg(res.data.message)
            }
            // <p>{errMsg}</p>
        })
    }

    function User(e) {
        const newuser = { ...user }
        newuser[e.target.id] = e.target.value
        setUser(newuser)
        // console.log(newuser)
    }
    return (
        <>
            <form onSubmit={(e) => submit(e)}>
                <Row>
                    <Col lg={4}>
                        <label>Plan Title</label>
                        <input placeholder='Plan Name' onChange={(e) => User(e)} id="plan_title" value={user.plan_title} className='form-control mb-3' type='text'></input>
                    </Col>
                    <Col lg={4}>
                        <label>Validity :</label>
                        <input placeholder='Enter Validity' onChange={(e) => User(e)} id="validity" value={user.validity} className='form-control mb-3' type='text'></input>
                    </Col>
                    <Col lg={4}>
                        <label>Paid Price</label>
                        <input placeholder='$' onChange={(e) => User(e)} id="paid_price" value={user.paid_price} className='form-control mb-3' type='text'></input>
                    </Col>
                </Row>
                <Row>
                    <Col lg={4}>
                        <label>Plan Data :</label>
                        <input placeholder='Plan Data' onChange={(e) => User(e)} id="plan_data" value={user.plan_data} className='form-control mb-3' type='text'></input>
                    </Col>
                </Row>
                <Row>
                    <Col lg={12}>
                        <button type='submit'>Submit</button>
                    </Col>
                </Row>
            </form>
        </>
    )
}
